import React from 'react';
import {Grid, Typography, useMediaQuery} from "@material-ui/core";
import {useTheme, makeStyles} from "@material-ui/core";
import RevolutionFeature from "./RevolutionFeature/RevolutionFeature";
import vision from '../../assets/vision.svg';
import consult from '../../assets/consultationIcon.svg';
import mockup from '../../assets/mockupIcon.svg';
import review from '../../assets/reviewIcon.svg';
import design from '../../assets/designIcon.svg';
import build from '../../assets/buildIcon.svg';
import launch from '../../assets/launchIcon.svg';
import maintain from '../../assets/maintainIcon.svg';
import iterate from '../../assets/iterateIcon.svg';
import CallToAction from "../LandingPage/CallToAction/CallToAction";

const useStyles = makeStyles((theme) => ({
    revolution: {
        marginTop: '2em'
    },
    revolutionHeading: {
        fontFamily: 'Pacifico',
    },
    GridRowContainer: {
        paddingLeft: '5em',
        paddingRight: '5em',
        [theme.breakpoints.down('sm')]: {
            paddingLeft: '2.5em',
            paddingRight: '2.5em',
        },
        [theme.breakpoints.down('xs')]: {
            paddingLeft: '1em',
            paddingRight: '1em',
        }
    },
    techAnimation: {
        marginTop: '10em',
        marginBottom: '10em'
    }
}))

const RevolutionPage = () => {
    const classes = useStyles();
    const theme = useTheme();

    const matchesMd = useMediaQuery(theme.breakpoints.down('md'));
    const matchesXs = useMediaQuery(theme.breakpoints.down("xs"));

    return (
        <Grid container>

            <Grid item container alignItems={'center'} align={'center'} justify={matchesMd ? 'center' : null}
                  className={`${classes.revolution} ${classes.GridRowContainer}`}>
                <Typography variant={'h2'} className={classes.revolutionHeading}>
                    The Revolution
                </Typography>
            </Grid>

            <Grid item container>
                <RevolutionFeature imageLeft
                                   maxImageWidth={'40em'}
                                   maxTextWidth={'40em'}
                                   heading={'Vision'}
                                   p1={` The rise of computers, and subsequently the Internet, has completely altered every aspect of
            human life. This has increased our comfort, broadened our connections, and reshaped how we
            view the world`}
                                   p2={` What once was confined to huge rooms and teams of engineers now resides in every single one
            of
            our hands. Harnessing this unlimited potential by using it to solve problems and better
            lives is
            at the heart of everything we do.`}
                                   p3={`We want to help businesses capitalize on the latest and greatest technology. The best way to predict the future is to be the one building it, and we want to help guide the world into this next chapter of technological expansion, exploration, and innovation.`}
                                   p4={`By holding ourselves to rigorous standards and pristine quality, we can ensure you have the absolute best tools necessary to thrive in this new frontier.`}
                                   p5={`We see a future where every individual has personalized software custom tailored to their lifestyle, culture, and interests, helping them overcome life’s obstacles. Each project is a step towards that goal.`}
                                   image={vision}

                />

            </Grid>

            <Grid item container className={classes.techAnimation}>
                <RevolutionFeature
                    maxTextWidth={'40em'}
                    heading={'Technology'}
                    p1={`In 2013, Facebook invented a new way of building websites. This new system, React.js, completely revolutionizes the process and practice of website development.`}
                    p2={`Instead of chaining together long individual pages, like traditional websites, React websites are built with little chunks of code called components. These components are faster, easier to maintain, and are easily reused and customized, each serving a singular purpose`}
                    p3={`Two years later they shocked the world by releasing a similar system, React Native, for producing iOS and Android apps. Instead of having to master two completely separate development platforms, you can leverage the knowledge you already possessed from building websites and reapply it directly! This was a huge leap forward.`}
                    p4={`This technology is now being used by companies like AirBnB, Microsoft, Netflix, Pinterest, Skype, Tesla, UberEats, and when Facebook purchased Instagram large portions of it were even rebuilt using React.`}
                    p5={`Developers have since built on top of these systems by automating project setup and deployment, allowing creators to focus as much as possible on their work itself.`}
                    p6={`These technical advancements translate into savings by significantly reducing the workload and streamlining the workflow for developing new pieces of software, while also lowering the barrier to entry for mobile app development.`}
                    p7={`This puts personalization in your pocket — faster, better, and more affordable than ever before.`}
                    animation
                />
            </Grid>
            <Grid item container justify={'center'}>
                <Typography variant={'h4'} gutterBottom>
                    Process
                </Typography>
            </Grid>

            <Grid style={{background: '#ccc', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    textColor={'white'}
                    headingColor={'black'}
                    heading={'Consultation'}
                    p1={` Our process begins the moment you realize you need a piece of technology for your business. Whether you already have an idea for where to start and what to do, or if you just know you want to step things up, our initial consultation will help you examine your business holistically to find the best solutions.`}
                    p2={` Detailed notes will be taken on your requirements and constraints, while taking care to identify other potential areas for consideration.`}
                    p3={`Cutting-edge advancements in machine learning like object detection and natural language processing allow computers to do things previously unimaginable, and our expertise and intuition will help usher you into this new future of possibilities.`}
                    image={consult}
                    height={'90em'}
                    maxImageWidth={'600px'}
                />
            </Grid>

            <Grid style={{background: '#FF7373', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Mockup'}
                    p1={`After we settle on the best path forward and decide on a solution to pursue, details like the cost and timeline will be finalized.`}
                    p2={`Then it’s time for us to start on your minimum viable product. That’s just a fancy term for a mockup, which doesn’t include colors, images, or any other polished design elements, but captures the essential layout structure and functionality.`}
                    p3={`This helps us understand and refine the solution itself before getting distracted by specifics and looks.`}
                    image={mockup}
                    height={'90em'}
                    maxImageWidth={'1000px'}
                />
            </Grid>
            <Grid style={{background: '#39B54A', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Review'}
                    p1={`Before moving any farther we come back to you with our progress. This gives you the freedom to discuss any changes you may want or any ideas you may have come up with before any heavy lifting has been done.`}
                    p2={`We give you an interactive demonstration of the mockups, thoroughly explaining the thought process that went into each screen and every anticipated feature.`}
                    p3={`Once you’re completely satisfied with the vision for our solution we get down to the nitty gritty, fine-details of design.`}
                    image={review}
                    height={'60em'}
                    maxImageWidth={'20em'}
                />
            </Grid>
            <Grid style={{background: '#A67C52', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Design'}
                    p1={`Using the mockups and notes taken during the consultation as guides, we will start ironing out what the final product will look like. This also involves using any brand material like fonts, colors, and logos to extend the experience you’re already familiar with.`}
                    p2={`No aspect is superfluous, and care will be taken with every decision.`}
                    image={design}
                    height={matchesXs ? '35em' : '50em'}
                    maxImageWidth={'1000px'}
                />
            </Grid>

            <Grid style={{background: '#FBB03B', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Build'}
                    p1={`Here’s where we get down to business.`}
                    p2={`Engineering begins after your approval on the final designs. We start by scaffolding out the project on a high level, prioritizing some areas over others.', 'Each area is then developed in order of importance until ready to be connected to the next piece.`}
                    p3={`Typically the backend, behind the scenes operations are completed first. Once all the services are in place we can then create the front end, user side of things.`}
                    p4={`Finishing the application doesn’t mean we’re done though, because we use extensive testing to guarantee compatibility with all intended devices.`}
                    p5={`Only after our rigorous examinations will we accept a product as finished, then pushing it through the production pipeline. This produces an optimized, compressed, consumer version of the code and assets ready for deployment.`}
                    image={build}
                    height={matchesXs ? '80em' : '90em'}
                    maxImageWidth={'800px'}
                />
            </Grid>

            <Grid style={{background: '#C1272D', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Launch'}
                    p1={`The moment we’ve all been waiting for.`}
                    p2={`When construction comes to a close you’re the first one to know. We’ll give our final demonstration to show off your shiny new software in the wild so you know exactly how it will look to your users.`}
                    p3={`When you say the word, we press the button and launch your project out to the public. We’re there to ensure everything goes to plan so you can start reaping the rewards of your technological investment immediately.`}
                    image={launch}
                    height={matchesXs ? '80em' : '100em'}
                    maxImageWidth={'200px'}
                />
            </Grid>

            <Grid style={{background: '#8E45CE', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Maintain'}
                    p1={`Our work doesn’t end there.`}
                    p2={`After a successful launch we keep in close contact to listen to feedback and hear how the project is being received.`}
                    p3={`From there on out we make sure your application is kept up to date and taking advantage of the best features and practices available. When new developments arise or new techniques are discovered in future projects, we will implement those advancements in your project as part of our routine maintenance.`}
                    image={maintain}
                    height={matchesXs ? '80em' : '90em'}
                    maxImageWidth={'300px'}
                />
            </Grid>

            <Grid style={{background: '#29ABE2', paddingTop: '10em', paddingBottom: '10em'}} item container>
                <RevolutionFeature
                    maxTextWidth={'20em'}
                    headingColor={'black'}
                    textColor={'white'}
                    heading={'Iterate'}
                    p1={`The cycle repeats whenever you come up with a new idea for extending your current project, or come up with a brand new system entirely.`}
                    p2={`By planning for future features and changes we can build and evolve your application over time. As new use cases and customer needs develop we can respond with continuous integration of new content.`}
                    p3={`Our iterative process will keep you current and competitive, allowing you to quickly implement changes instead of waiting months for a single update.`}
                    image={iterate}
                    height={matchesXs ? '50em' : '70em'}
                    maxImageWidth={'auto'}
                />
            </Grid>
            <Grid item container>
                <CallToAction/>
            </Grid>


        </Grid>
    );
};

export default RevolutionPage;
